.navBar{
  align-items: center;
  display: flex;
  flex: 1;
  justify-content: space-between;
  padding: 1% 3% 0% 3%
}

.naviButton {
  border-width: 0;
  background-color: transparent;
  color: #E1E7E4;
  font-size: 2ch;
  outline: none;
}

.naviButtonSelected{
  border-width: 0;
  background-color: transparent;
  color: #dcf3ff;
  font-size: 2ch;
  outline: none;
}

.naviButton:hover{
  border-width: 0;
  background-color: transparent;
  color: #dcf3ff;
  font-size: 2ch;
  outline: none;
}

@font-face{
  font-family: 'Assistant', sans-serif;
  src: url('https://fonts.googleapis.com/css2?family=Assistant')
}